// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import axios from 'axios'
import VCharts from 'v-charts'
import 'v-charts/lib/style.css'
import 'element-ui/lib/theme-chalk/index.css' // default theme
import locale from '../node_modules/element-ui/lib/locale/lang/en' // import en env
// import vueGooglemap from 'vue2-googlemap'

Vue.config.productionTip = false
Vue.use(ElementUI, {
  size: 'small',
  locale
})

Vue.use(VCharts)
Vue.prototype.$axios = axios

// Vue.use(vueGooglemap)
// vueGooglemap.initGooglemap({
//   key: 'your GOOGLE_MAPS_API_KEY',
//   language: 'zh-CN',
//   v: '3'
// })

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



