import Vue from 'vue'
import Router from 'vue-router'
import VidarDbDemo from '@/components/VidarDbDemo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'VidarDbDemo',
      component: VidarDbDemo
    }
  ]
})



