'use strict'

import axios from 'axios'
import qs from 'qs'
import { Message } from 'element-ui'
const Axios = axios.create({
  timeout: 60000, //  Request timeout
  baseURL: process.env.BASE_API // api的base_url
})
Axios.interceptors.request.use(config => {
  return config
}, error => {
  Message.error({ message: 'Request timeout!' })
  return Promise.reject(error)
})

Axios.interceptors.response.use(response => {
  const res = response.data
  return res
}, error => {
  if (error.response) {
    if (error.response.status === 306) {
      const loginUrl = error.response.data + '&version=v3&callback=' + encodeURIComponent(window.location.href)
      window.location = loginUrl
      return
    } else if (error.response.status === 504 || error.response.status === 404) {
      Message.error({ message: 'Server Internal Error, Please contact admin!' })
    } else if (error.response.status === 403) {
      Message.error({ message: 'Perm Error, Please contact admin!' })
    } else if (error.response.status === 400) {
      console.log(error.response.data)
      // Message.error({ message: '数据获取失败，请联系管理员' })
    }
  } else {
    Message.error({ message: 'Unknown Error!' })
  }
  return Promise.resolve(error.response)
})

function post (url, params) {
  return new Promise((resolve, reject) => {
    return Axios({
      method: 'post',
      baseURL: process.env.BASE_API,
      url,
      data: qs.stringify(params) || '',
      withCredentials: true,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(res => {
      resolve(res)
    }).catch(err => {
      console.log(err)
      reject(err)
    })
  })
}

const get = (url, params, credential) => {
  if (typeof (credential) === 'undefined') { // 默认是加验证，即访问后端接口使用，若访问第三方开放接口，需设定为false
    credential = true
  }
  return new Promise((resolve, reject) => {
    return Axios({
      method: 'get',
      url: url,
      params: params,
      withCredentials: credential,
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(res => {
      resolve(res)
    }).catch(err => {
      console.log(err)
      reject(err)
    })
  })
}
const fileRequest = (url, params) => {
  return new Promise((resolve, reject) => {
    Axios({
      method: 'post',
      url: url,
      data: qs.stringify(params) || '',
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then(res => {
      resolve(res)
    }).catch(err => {
      console.log(err)
      reject(err)
    })
  })
}
export default {
  post,
  get,
  fileRequest
}



