import http from '@/utils/http'

// get task result.
export function getTaskResult (params) {
  return http.get('/api/v1/task/taskId/result', params)
}

// creat task
export function createTask (params) {
  return http.post('/auth/v1/task/create', params)
}

// stop task
export function stopTask (params) {
  return http.post('/auth/v1/task/stop', params)
}

// delete task
export function deleteTask (params) {
  return http.post('/auth/v1/task/delete', params)
}



