<template>
  <div class="vidardb">
    <el-row :gutter="20">
      <el-col :span="14">
        <span style="font-weight: bold">Maximum Trip Fare Updated Every 100 Records</span>
      </el-col>
      <el-col :span="10">
        <span style="font-weight: bold">Data Flow of Taxi Trip Orders</span>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="14">
          <div style="border:1px solid #EBEEF5">
            <ve-line :data="biChartData"
                     :extend="extend"
                     :grid="grid"
                     :mark-line="markLine"
                     :settings="vchartsLineConfig"
                     :mark-point="markPoint"></ve-line>
          </div>
      </el-col>
      <el-col :span="10">
        <el-table :data="rawTableData" :header-cell-style="{background:'#eef1f6',color:'#606266'}" stripe border style="width: 100%">
          <el-table-column
            prop="trip_start_timestamp"
            label="Trip Start Time">
          </el-table-column>
          <el-table-column
            prop="taxi_id"
            label="Taxi ID">
          </el-table-column>
          <el-table-column
            prop="row_vec"
            label="Pickup Location">
          </el-table-column>
          <el-table-column
            prop="fare"
            label="Trip Fare">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <br>
    <el-row :gutter="20">
      <el-col :span="14">
        <span style="font-weight: bold">Clustering of Pickup Location</span>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="14">
         <el-table :data="aiTableData" :header-cell-style="{background:'#eef1f6',color:'#606266'}" stripe border style="width: 100%">
            <el-table-column
              prop="id"
              label="Trip ID"
              min-width="60">
            </el-table-column>
            <el-table-column
              prop="row_vec"
              label="Pickup Location">
            </el-table-column>
            <el-table-column
              prop="cluster_id"
              label="Cluster ID">
            </el-table-column>
          </el-table>
      </el-col>

      <el-col :span="10">
        <el-row >
          <div style="height: 272px;">
            <el-table :data="biSql" :header-cell-style="{background:'#eef1f6',color:'#606266'}" highlight-current-row stripe border style="width: 100%">
              <el-table-column
                prop="value"
                label="Maximum Trip Fare"
                min-width="60" max-high="40">
                <el-image :src="srcBiImg"></el-image>
              </el-table-column>
            </el-table>
            <br/>
            <el-table :data="aiSql" :header-cell-style="{background:'#eef1f6',color:'#606266'}" stripe border style="width: 100%">
              <el-table-column
                prop="value"
                label="Clustering Pickup Location"
                min-width="60">
                <el-image :src="srcAiImg"></el-image>
              </el-table-column>
            </el-table>
          </div>
        </el-row>
        <br/> <br/> <br/>
        <el-row>
          <div style="align:center;margin-top:50px">
            <el-button type="primary" style="margin-right:30px" @click="start" round>start</el-button>
            <el-button type="danger" @click="stop" :disabled="disableStopBtn" round>stop</el-button>
          </div>
        </el-row>
      </el-col>
    </el-row>

<!--    <el-row :gutter="20">-->
<!--      <el-col :span="14">-->
<!--        <div style="height: 500px">-->
<!--          <vue-googlemap :center='center'></vue-googlemap>-->
<!--        </div>-->
<!--      </el-col>-->
<!--    </el-row>-->
  </div>
</template>

<script>
import { getTaskResult, createTask, stopTask } from '@/api/data'
import numerify from 'numerify'
import numerifyCurrency from 'numerify/lib/plugins/currency.umd.js'
numerify.register('currency', numerifyCurrency)

export default {
  name: 'VidarDbDemo',
  data () {
    this.theme = {
      line: {
        smooth: true
      }
    }
    this.extend = {
      series: {
        smooth: true
      }
      // 'xAxis.0.axisLabel.rotate': -2
    }
    this.grid = {
      // show: true,
      left: 15,
      right: 35
    }
    this.markLine = {
      data: [
        {
          name: 'average',
          type: 'average'
        }
      ]
    }
    this.markPoint = {
      data: [
        {
          name: 'max',
          type: 'max'
        }
      ]
    }
    this.vchartsLineConfig = {
      legendName: {
        'fare': 'maximum trip fare'
      },
      legend: {y: 'bottom', x: 'center'},
      // yAxisName: ['U.S. dollar'],
      yAxisType: ['$ 0,0.00'],
      dataType: function (v) {
        return v + ' $'
      }
    }
    this.chartSettings = {
      key: 'oBvDtR6nzWtVchkY4cLHtnah1VVZQKRK',
      bmap: {
        center: [120, 30],
        zoom: 14,
        roam: true,
        mapStyle: {}
      }
    }
    this.chartTooltip = { show: true }
    // ---------------------------------------------
    return {
      msg: 'Welcome to VidarDB demo App',
      taskId: '',
      interval_id: 0,
      biChartData: {
        columns: ['trip_start_timestamp', 'fare'],
        rows: []
      },
      biSqlData: [],
      rawTableData: [],
      aiTableData: [],
      aiSqlData: [],
      biSql: [],
      aiSql: [],
      disableStopBtn: false,
      srcBiImg: '',
      srcAiImg: '',
      center: [39.9, 116.39]
      // aiMapData: [
      //   {
      //     type: 'scatter',
      //     coordinateSystem: 'bmap',
      //     data: [
      //       [120, 30, 1] // 经度，维度，value，...
      //     ]
      //   }
      // ]
    }
  },
  created () {
    this.taskId = localStorage.getItem('task_id')
    if (this.taskId && this.interval_id === 0) {
      this.fetchTaskResult(this.taskId)
      this.interval_id = setInterval(() => {
        this.fetchTaskResult(this.taskId)
      }, 2000)
    } else {
      this.disableStopBtn = true
    }
  },
  methods: {
    fetchTaskResult (taskId) {
      getTaskResult({task_id: taskId}).then(res => {
        if (res.code === 0) {
          this.biChartData.rows = res.data.bi
          this.aiTableData = res.data.ai
          this.rawTableData = res.data.raw
          this.biSql = res.data.biSql
          this.aiSql = res.data.aiSql
          this.srcBiImg = '../assets/static/max.jpg'
          this.srcAiImg = '../assets/static/kmeans.jpg'
          // this.aiMapData.data = res.data.aiMap
          if (res.data.status === 'finish') {
            this.disableStopBtn = true
            this.$notify.success({
              message: 'The task is finished.',
              duration: 5000
            })
            clearInterval(this.interval_id)
            this.interval_id = 0
          } else {
            this.disableStopBtn = false
          }
        } else {
          this.$notify.error({
            title: 'Error',
            message: res.data
          })
          clearInterval(this.interval_id)
          this.interval_id = 0
        }
      })
    },
    beforeDestroy () {
      clearInterval(this.interval_id)
      this.interval_id = 0
    },
    start () { // get task id，storage localstorage，add timer and query task result.
      if (this.interval_id > 0) {
        this.$notify.warning('There are already tasks running.')
        return
      }
      createTask().then(res => {
        if (res.code === 0) {
          this.$notify.success('Create task success')
          this.taskId = res.data.task_id
          localStorage.setItem('task_id', this.taskId)
          this.fetchTaskResult(this.taskId)
          this.interval_id = setInterval(() => {
            this.fetchTaskResult(this.taskId)
          }, 2000)
        } else {
          this.$notify.error({
            title: 'Error',
            message: res.data
          })
        }
      })
    },
    stop () { // stop the task，close timer，clear localstorage
      stopTask({task_id: this.taskId}).then(res => {
        if (res.code === 0) {
          this.$notify.success('Stop task success')
          clearInterval(this.interval_id)
          this.interval_id = 0
          this.disableStopBtn = true
          localStorage.removeItem('task_id')
        } else {
          this.$notify.error({
            title: 'Error',
            message: res.data
          })
        }
      })
    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.el-table tbody tr:hover>td {
  color: #ffff;
}
</style>



